
import Countup from 'vux/src/components/countup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import Group from '../components/User/Group'
import Cell from '../components/User/Cell'
import { mapActions, mapGetters } from 'vuex'
export default {
data () {
return {
shareMaskVisible: false
}
},
components: {
Countup,
Group,
Cell,
XDialog
},
beforeRouteEnter (to, from, next) {
document.title = '个人中心'
next()
},
created () {
this.$vux.loading.show({
text: '正在加载'
})
this.getUserInfo().then((res) => {
this.$vux.loading.hide()
}).catch(() => {
this.$vux.loading.hide()
})
/* if (Object.keys(this.$store.getters['user/userInfo']).length === 0) {
this.$vux.loading.show({
text: '正在加载'
})
this.getUserInfo().then((res) => {
this.$vux.loading.hide()
}).catch(() => {
this.$vux.loading.hide()
})
} */
},
computed: {
...mapGetters('user', [
'userInfo'
]),
getRole () {
if (this.userInfo.isMM > 0) {
return '商户'
} else if (this.userInfo.isAgent > 0) {
return '代理'
} else if (this.userInfo.is_manager > 0) {
return '管理员'
} else {
return ''
}
},
isCityPingYin () {
if (this.userInfo && this.userInfo.city) {
if (/^[\u4E00-\u9FA5]+$/.test(this.userInfo.city)) {
return true
} else {
return false
}
} else {
return false
}
}
},
methods: {
...mapActions('user', [
'getUserInfo'
])
}
}



